// fonts
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Schoolbell&display=swap');

h1 {
  font-family: 'Poppins', sans-serif;
}
.honey {
  font-family: 'Schoolbell', serif;
  background: url('https://img.freepik.com/premium-photo/pink-glitter-texture-background_322958-1041.jpg');
  background-repeat: repeat;
  background-position: center center;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
h2 {
  span.honey {
    font-size: 32px;
    font-weight: 600;
    transform: rotate(30deg);
  }
}
.page-intro {
  .sub-title {
    margin-bottom: 0;
  }
}
.menu-list {
  li {
    list-style: none;
  }
  padding-left: 0;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
